import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';

const Header = props => {
  const { branding } = props;
  return (
    <nav className="navbar navbar-expand-md navbar-dark bg-primary mb-4 py-0">
      <div className="container">
        <Link to="/" className="navbar-brand">
          <i className="fas fa-dumbbell" style={{ paddingRight: '.3em' }} />
          {branding}
        </Link>
        <div>
          <ul className="navbar-nav mr-auto">
            <li className="nav-item">
              <Link to="/" className="nav-link">
                <i className="fas fa-home" style={{ paddingRight: '.3em' }} />
                Home
              </Link>
            </li>
            <li className="nav-item">
              <Link to="/workout/add" className="nav-link">
                <i
                  className="fas fa-plus-circle"
                  style={{ paddingRight: '.3em' }}
                />
                Add
              </Link>
            </li>

            <li className="nav-item">
              <Link to="/about" className="nav-link">
                <i
                  className="fas fa-question-circle"
                  style={{ paddingRight: '.3em' }}
                />
                About
              </Link>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  );
};
export default Header;

Header.propTypes = {
  branding: PropTypes.string.isRequired
};



