import React, { Component } from 'react';
import { Consumer } from '../../Context';
import DailyWorkout from './DailyWorkout';
class Day extends Component {
  render() {
    return (
      <Consumer>
        {value => {
          const { users } = value;
          const { name } = users;
          let dt = new Date();
          let today = dt.getDay().toString();
          const arr = users.workouts.filter(word => word.day === today);
          let dDay = '';
          switch (today) {
            case '1':
              dDay = 'Monday';
              break;
            case '2':
              dDay = 'Tuesday';
              break;
            case '3':
              dDay = 'Wednesday';
              break;
            case '4':
              dDay = 'Thurday';
              break;
            case '5':
              dDay = 'Friday';
              break;
            case '6':
              dDay = 'Sarturday';
              break;
            case '0':
              dDay = 'Sunday';
              break;
            default:
              dDay = 'Day off';
          }
          return (
            <div className="container">
              {' '}
              <h1>{name}</h1>
              <h2> {dDay}</h2>
              {arr.map(workDay => (
                <DailyWorkout key={workDay.id} workoutDay={workDay} />
              ))}
            </div>
          );
        }}
      </Consumer>
    );
  }
}
export default Day;



